/***********
   GENERAL
   ********/

* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    transition-property: opacity, transform, -webkit-transform;
    transition-delay: .2s;
}

body {
    font-family: 'Alice', serif !important;
    background-color: rgb(255, 254, 253);
    font-size: 1rem;
    /* color: rgb(77, 77, 77) !important; */
    overflow-y: auto;
}

