.img-fluid {
    width: 100%;
    transition: -webkit-transform 1s cubic-bezier(.7, -0.39, .31, 1.38);
    transition: transform 1s cubic-bezier(.7, -0.39, .31, 1.38);
    transition: transform 1s cubic-bezier(.7, -0.39, .31, 1.38), -webkit-transform 1s cubic-bezier(.7, -0.39, .31, 1.38);
    box-shadow: 0.1rem 2rem 4rem rgba(0, 0, 0, 0.4);
}

.img-fluid:hover {
