a {
    text-decoration: none;
    color: #111;
    cursor: pointer!important;
}

a:hover {
    color: #0f0f0f;
    text-decoration: none;
    cursor: pointer;
}

.hoverLine {
    text-decoration: none;
    color: black;
    position: relative;
}

.hoverLine:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -5px;
    left: 0;
    background-color: #1ab394;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    transition: all 0.4s ease-in-out 0s;
}

.hoverLine:hover:before {
    text-decoration: none;
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

